import * as path from "path";
import * as React from "react";
import * as ReactDOM from "react-dom";

import { PageContainer } from "./components/PageContainer";
import { DataFilePath, AllColumns, LookUpStateCode, StateDict, DbName, GeoField } from "./constants";


function getGroupByQuery(column: string, filterColumnRaw?: string, valuesString?: string) {
  let s = "";
  const filterColumn = filterColumnRaw
    ? filterColumnRaw === "geo"
      ? GeoField
      : filterColumnRaw
    : filterColumnRaw
    ;
  if (filterColumn && valuesString) {
    s = `SELECT COUNT(), ${column}, 'yes' as is_overview FROM ${DbName} WHERE ${filterColumn} IN (${valuesString}) GROUP BY ${column} UNION SELECT COUNT(), ${column}, 'no' as is_overview FROM ${DbName} GROUP BY ${column}`
  } else {
    s = `SELECT COUNT(), ${column}, 'no' as is_overview FROM  ${DbName} GROUP BY ${column}`
  }
  console.log("SQL", s)
  return s;
}

function throttle(fn: any, wait: number) {
  let time = Date.now();
  return function() {
    if ((time + wait - Date.now()) < 0) {
      fn();
      time = Date.now();
    }
  };
}


async function setUpSQLJSWorker() {
  // https://sql-js.github.io/sql.js/dist/worker.sql-wasm.js
  // somehow this resolving is CRITICAL!!!
  // if the path is supported directly the wasm doesn't get parsed properly!!!!!
  // wasted 3 hours on this...
  // const jsFile = path.resolve(__dirname, "./dist/worker.sql-wasm.js");
  // console.log("path", jsFile);

  window.addEventListener("beforeunload", function (e) {
    var confirmationMessage = 'If you leave before saving, your changes will be lost.';
    (e || window.event).returnValue = confirmationMessage; //Gecko + IE
    return confirmationMessage; //Gecko + Webkit, Safari, Chrome etc.
});

  // let's also setup the logger here
  // let logger: any[] = [];
  (window as any).expLogging = [];
  function logScroll() {
    (window as any).expLogging.push({"scroll": "", "time": Date.now()})
  }

  window.addEventListener("scroll", throttle(logScroll, 1000), true);

  // set up a download function
  function downloadExpData() {
    let dataStr = "data:text/json;charset=utf-8," + encodeURIComponent(JSON.stringify((window as any).expLogging));
    let a = document.createElement("a");
    a.href = dataStr;
    const d = new Date();
    a.setAttribute("download", `chronicles_log_${d}.json`);
    a.click();
  }
  document.addEventListener("keydown", function(e) {
    if (e.keyCode == 83 && (navigator.platform.match("Mac") ? e.metaKey : e.ctrlKey)) {
      e.preventDefault();
      downloadExpData();
    }
  }, false);
  (window as any).downloadExpData = downloadExpData;

  // let worker = new Worker("/chronicles-fires/sql-dist/worker.sql-wasm.js");
  let worker = new Worker("/chronicles-protest/sql-dist/worker.sql-wasm.js");

  // let worker = new Worker("/sql-dist/worker.sql-wasm.js");

  const response = await fetch(DataFilePath);
  const bufferRaw = await response.arrayBuffer();
  const buffer = new Uint8Array(bufferRaw);


  worker.onerror = e => console.log("Worker error: ", e);
  worker.postMessage({
    id: 'init',
    action: "open",
    buffer: buffer, /*Optional. An ArrayBuffer representing an SQLite Database file*/
  });

  let currentId = 0;

  const addFilter = (column: string, values: string[]) => {
    const valuesString = values.map(v => `'${v}'`).join(",");
    function postMessage(c: string, step: number) {
      return () => {
        worker.postMessage({
          id: `${step}-${c}`,
          action: "exec",
          sql: getGroupByQuery(c, column, valuesString),
        });
      }
    }
  
    for (let c of AllColumns) {
      if ((c == column) || ((column === "geo") && ((c === "State") || (c === "country")))) {
        postMessage(c, currentId)();
      } else {
        const f = postMessage(c, currentId);
        const delay = Math.random() * 2000 + 5000;
        // const delay = 100000;
        setTimeout(f, delay);
      }
    }

    (window as any).expLogging.push({"query": column, "values": values, "time": Date.now()});

    currentId += 1;
  }

  let containerRef: PageContainer;
  ReactDOM.render(
    <PageContainer addFilter={addFilter} ref={(r) => containerRef = r}/>,
    document.getElementById("wrapper")
  );
  
  worker.onmessage = () => {
    console.log("Database opened");
    worker.onmessage = event => {
      // console.log("received", event.data.id, event.data); // The result of the query
      const v = event.data.id.split("-");
      const column = v[1];
      const jsonValue = [];
      for (let value of event.data.results[0].values) {
        let k = {};
        k[column] = value[1];
        if (column == "State") {
          k["id"] = LookUpStateCode[StateDict[value[1]]];
        }
        k["count"] = value[0];
        k["is_overview"] = value[2];
        jsonValue.push(k);
      }
      containerRef.updateData(v[0], v[1], jsonValue);
    };

    for (let c of AllColumns) {
      worker.postMessage({
        id: `${currentId}-${c}`,
        action: "exec",
        sql: getGroupByQuery(c),
      });  
    }
    currentId += 1;
  };

}

setUpSQLJSWorker();