import React from "react";
import { VLChart } from "./VLChart";

import { AllColumns, VisKind, SortColumns, IsThumbNailView } from "../constants";
import { Spinner } from "./Spinner";

interface DashboardProps {
  step: number;
  isCurrentStep: boolean;
  selectStep: () => void;
  isPrimary: boolean;
  data?: {[column: string]: any[]};
  addFilter: (column: string, values: string[]) => void;
}


function getMapSpec() {
  // @ts-ignore
  if (VisKind == "protest") {
    return {
      "$schema": "https://vega.github.io/schema/vega-lite/v4.json",
      "description": "the population per state, engineers per state, and hurricanes per state",
        "width": 500,
        "height": 300,
        "data": {
          "values": [1]
        },
        "transform": [
          {
            "lookup": "country",
            "from": {
              "data": {
                "url": "https://cdn.jsdelivr.net/npm/world-atlas@2/countries-110m.json",
                "format": {
                  "type": "topojson",
                  "feature": "countries"
                }
              },
              "key": "properties.name"
            },
            "as": "geo"
          },
          {"filter": "isValid(datum.geo)"}
        ],
        "projection": {"type": "naturalEarth1"},
        "mark": "geoshape",
        "selection": {
          "select": {"type": "multi", "empty": "none"}
        },
        "encoding": {
          "shape": {
            "field": "geo",
            "type": "geojson"
          },
          "color": {
            "field": "count",
            "type": "quantitative"
          },
          "tooltip": {
            "field": "geo.properties.name",
            "type": "nominal"
          }
        }
    }
  }
  return {
    "$schema": "https://vega.github.io/schema/vega-lite/v4.json",
    "width": 500,
    "height": 300,
    "data": {"values": [1]},
    "transform": [
      {"filter": "isValid(datum.id)"},
      {
        "lookup": "id",
        "from": {
          "data": {
            "url": "dist/us-10m.json",
            "format": {
              "type": "topojson",
              "feature": "states"
            }
          },
          "key": "id"
        },
        "as": "geo"
      }
    ],
    "projection": {"type": "albersUsa"},
    "mark": "geoshape",
    "selection": {
      "select": {"type": "multi", "empty": "none"}
    },
    "encoding": {
      "shape": {"field": "geo", "type": "geojson"},
      "color": {"field": "count", "type": "quantitative"},
      "tooltip": {"field": "State", "type": "nominal"}
    }
  }
  
}

function getDistSpec(column: string) {
  let spec = {
    "$schema": "https://vega.github.io/schema/vega-lite/v4.json",
    "data": {"values": [1]},
    "encoding": {
      "x": {
        "field": column,
        "type": "ordinal"
      },
      "y": {"field": "count", "type": "quantitative", "stack": 0},
      "opacity": {"value": 0.5}
    },
    "layer": [
      {
        "mark": {"type": "bar", "tooltip": true},
        "encoding": {
          "color": {
            "field": "is_overview",
            "type": "nominal",
            "scale": {"range": ["#003E6B", "#9FB3C8"], "domain": ["yes", "no"]},
            "legend": 0
          }
        },
        "selection": {
          "select": {"type": "multi", "encodings": ["x"], "empty": "none"}
        }
      },
      {
        "mark": {"type": "bar", "tooltip": true},
        "transform": [{"filter": {"selection": "select"}}],
        "encoding": {
          "color": {
            "field": "is_overview",
            "type": "nominal",
            "scale": {"range": ["#fd8d3c", "#fdae6b"], "domain": ["yes", "no"]},
            "legend": 0
          }
        }
      }
    ],
    "resolve": {"scale": {"color": "independent"}},
    "transform": [
      {
        "calculate": "datum.is_overview == 'no' ? datum['count'] : null",
        "as": "sort_order"
      }
    ]
  }
  // to make the height look even.
  if (column == "Cause") {
    spec["height"] = 145;
  }

  if (column === "participants_category") {
    spec["encoding"]["x"]["sort"] = ["50-99", "100-999", "1000-1999","2000-4999", "5000-10000",">10000"];
  }
  // "x": {"field": , "type": "ordinal", "sort": ["50-99", "100-999", "1000-1999","2000-4999", "5000-10000",">10000"]},

  if (SortColumns.includes(column)) {
    spec["encoding"]["x"]["sort"]= {"field": "sort_order", "order": "descending"};
  }
  return spec;
}

function processDataForMap(data: any[]) {
  // if there is overview: "yes"
  if (!data) return data;
  const overview = data.filter((d) => d["is_overview"] === "yes");
  if (overview.length > 0) {
    return overview;
  }
  return data;
}


export const Dashboard = (props: DashboardProps) => {
  let charts = [];
  let spinner: any;
  const elementIdEnd = `${props.step}${props.isPrimary ? "p" : ""}`;
  if (props.data) {
    for (let c of AllColumns) {
      const aSpec = getDistSpec(c);
      aSpec["data"]["values"] = props.data[c];
      charts.push(<VLChart
        // @ts-ignore
        spec={aSpec}
        key={`${c}-${props.step}`}
        column={c}
        addFilter={props.addFilter}
        elementId={`${c}${elementIdEnd}`}/>);
    }
    const mapSpec =  getMapSpec();
    mapSpec["data"]["values"] = processDataForMap(props.data[AllColumns[AllColumns.length - 1]]);
    charts.push(<VLChart
      // @ts-ignore
      spec={mapSpec}
      key={`geo-${props.step}`}
      column={"geo"}
      addFilter={props.addFilter}
      elementId={`geo${elementIdEnd}`}/>);
  } else {
    spinner = <Spinner color={"red"} cx={100} cy={100} radius={30}/>
  }

  const selectedClass = props.isCurrentStep
    ? props.isPrimary
      ? ""
      : "selected" 
    : ""
    ;
  let overlay = props.isPrimary
    ? null
    : <div className="overlay" onClick={props.selectStep}></div>
    ;
  let content = <div
    className={`dashboard-inner`}
  ><div className="row">
      {charts.slice(0, AllColumns.length-1)}
    </div>
    <div className="row">
      {charts[AllColumns.length]}{charts[AllColumns.length-1]}
    </div></div>;
  return <div className={`dashboard  ${selectedClass}`} id={`step-${props.step}`}>
    {overlay}
    {spinner}
    <div className="dashboard-content">
      <p className="step-no">{props.step}</p>
      {content}
    </div>
  </div>
}