import React from "react";
import { Spec } from "vega";
import vegaEmbed from "vega-embed";

import { MULTICLICK_SIGNAL, GeoField } from "../constants";
import { Spinner } from "./Spinner";

interface VLChartProps {
  spec: Spec;
  elementId: string;
  column: string;
  addFilter: (column: string, values: string[]) => void;
}

interface VLChartState {
  showOnlyFiltered: boolean;
}

export class VLChart extends React.Component<VLChartProps, VLChartState> {
  constructor(props: any) {
    super(props);
    this.embed = this.embed.bind(this);
    this.toggleFilteredValues = this.toggleFilteredValues.bind(this);
    this.state = {
      showOnlyFiltered: false
    }
  }
  componentDidMount() {
    // console.log("embedding after mount", this.props.elementId);
    this.embed(); 
  }
  // I dont think this will actually updates
  componentDidUpdate(prevProps: VLChartProps) {
    // console.log("embedding after update", this.props.elementId);
    // see if the value changed
    if (this.doNotHaveData() != this.doNotHaveData(prevProps.spec)) {
      this.embed();
    }
  }

  embed(transform?: any, sortBy?: string) {
    if (this.doNotHaveData()) {
      return;
    }
    console.log("Emdeded", this.props.column, this.props.elementId);
    // const { spec } = this.props;
    let spec = this.props.spec;
    if (transform) {
      spec = Object.assign({}, this.props.spec);
      spec["transform"].push(transform);
      if ("sort" in spec["encoding"]["x"]) {
        spec["encoding"]["x"]["sort"]["field"] = sortBy;
      }
    }

    vegaEmbed(`#${this.props.elementId}`, spec, {actions: false, renderer: "svg"})
      .then((res: any) => {
        const cb = (signalName: string, value: any) => {
          console.log("itx value", value);
          if (this.props.column === "geo") {
            let derivedValues: string[] = [];
            for (let v of value["_vgsid_"]) {
              derivedValues.push(this.props.spec.data.values[v-1][GeoField]);
            }
            this.props.addFilter(this.props.column, derivedValues);
          } else {
            this.props.addFilter(this.props.column, value[this.props.column]);
          }
        }
        res.view.addSignalListener(MULTICLICK_SIGNAL, cb);
      })
      .catch((err: Error) => console.error(err));
  }

  doNotHaveData(testSpec?: Spec) {
    const spec = testSpec ? testSpec : this.props.spec;
    return !(spec.data.values && spec.data.values.length > 0);
  }

  toggleFilteredValues() {
    this.setState(prev => {
      if (prev.showOnlyFiltered) {
        // now need to show summary
        this.embed();
      } else {
        this.embed({
          "filter": "datum.is_overview == 'yes'"
        }, "count");
      }
      return {showOnlyFiltered: !prev.showOnlyFiltered};
    });
  }

  render() {
    let spinner;
    let className = `${this.props.column}-chart`;
    if (this.doNotHaveData()) {
      spinner = <Spinner color={"red"} cx={100} cy={100} radius={30}/>
      className += "-holder";
    }
    const f = this.toggleFilteredValues;
    // const showOnlyFiltered = this.state.showOnlyFiltered;
    // {showOnlyFiltered ? "show summary" : "hide summary"}
    const zoomButton = this.props.column !== "geo"
      ? <div><button onClick={() => f()}></button></div>
      : null;
    return <div className="chart-container">
      {zoomButton}
      <div id={this.props.elementId} className={className}>
      {spinner}
      </div>
    </div>
  }
}
