import * as React from "react";
import { Dashboard } from "./Dashboard";
import { Title, IsThumbNailView } from "../constants";

interface PageContainerProps {
  addFilter: (column: string, values: string[]) => void;
  // data: {[column: string]: any[]}
}

interface PageContainerState {
  currentStep: number;
  activeStep: number;
  runTimeData: {[step: number]: {[column: string]: any[]}}
}

function scrollThumnailRight() {
  const content = document.getElementById('thumbnail-secondary');
  let sl = content.scrollLeft;
  let cw = content.scrollWidth;
  // HACK, sigh, so the last component is appended _after_ the scrolling
  //     I should change the react lifecycle method but this is one-time-code.
  setTimeout(()=>{
    content.scrollTo(cw + sl + 5000, 0)
  }, 100);
}


export class PageContainer extends React.Component<PageContainerProps, PageContainerState> {
  constructor(props: any) {
    super(props);
    this.addFilter = this.addFilter.bind(this);
    this.updateData = this.updateData.bind(this);
    this.state = {
      runTimeData: {},
      currentStep: 0,
      activeStep: 0
    }
    const changeView = this.changeView.bind(this);

    document.onkeydown = function(evt: any) {
      evt = evt || window.event;
      if ((evt.keyCode == 39) || (evt.keyCode == 40)) {
        // console.log("next");
        evt.preventDefault();
        changeView(1);
      } else if ((evt.keyCode == 37) || (evt.keyCode == 38)) {
        // console.log("prev");
        evt.preventDefault();
        changeView(-1);
      }
    };
  }

  changeView(delta: number) {
    this.setState(prev => {
      const newStep = Math.min(Math.max(prev.activeStep + delta, 0), prev.currentStep);
      let element = document.getElementById(`step-${newStep}`);
      element.scrollIntoView();
      (window as any).expLogging.push({"changeView": delta, "time": Date.now()});
      return {activeStep: newStep};
    });
  }

  setViewStep(newStep: number) {
    this.setState(prev => {
      (window as any).expLogging.push({"setViewStep": newStep, "time": Date.now()});
      return {activeStep: newStep};
    });
  }

  updateData(step: number, column: string, data: any[]) {
    // console.log("update data called", step, column, data);
    this.setState(prevState => {
      if (step in prevState.runTimeData) {
        prevState.runTimeData[step][column] = data;
      } else {
        let newObject = {};
        newObject[column] = data;
        prevState.runTimeData[step] = newObject;
      }
      return {runTimeData: prevState.runTimeData};
    });
    // console.log("force update");
    // this.forceUpdate();
  }

  addFilter(column: string, values: string[]) {
    this.setState(prev => {
      const newStep = prev.currentStep + 1;
      this.props.addFilter(column, values);
      scrollThumnailRight();
      return {
        currentStep: newStep,
        activeStep: newStep
      };
    });
  }

  getDashboardByStep(i: number, isPrimary=false) {
    const data = this.state.runTimeData[i];
    // console.log("dashboard all data", this.state.runTimeData);
    // console.log("dashboard data", data);
    const isNull = data ? "no" : "yes";
    return <Dashboard
      key={`dashboard-${i}-${isNull}`}
      isCurrentStep={this.state.activeStep === i}
      step={i}
      selectStep={() => {this.setViewStep(i)}}
      isPrimary={isPrimary}
      addFilter={this.addFilter}
      data={data}
    />;

  }

  render() {
    let dashboards = [];
    if (IsThumbNailView) {
      // when thumbnail view, grow to the right
      for (let i = 0; i <= this.state.currentStep; i++) {
        dashboards.push(this.getDashboardByStep(i));
      }
    } else {
      for (let i = this.state.currentStep; i >= 0; i--) {
        dashboards.push(this.getDashboardByStep(i));
      }
    }

    const primaryView = IsThumbNailView
      ? this.getDashboardByStep(this.state.activeStep, true)
      : null
      ;

    const thumbClassName = IsThumbNailView
      ? "thumbnail-secondary"
      : ""
      ;
  
      
    return <div>
      {/* <h2>{Title} Visualization</h2> */}
        {primaryView}
      <div id={thumbClassName}>
        {dashboards}
      </div>
    </div>
  }
}